import React, { Component } from 'react';
import Header from '../components/Header';
import Footer from '../components/Footer';
import'./About.scss'

class About extends Component {
    render() {
        return (
            <div className='wrap'>
               <Header />
               <div className='company' >
                  <p>YTS</p>
                  <p>대표: 홍길동</p>
                  <p>주소: 서울 특별시 강남구 역삼동 11</p>
                  <p>개인정보 취급방침</p>
                  <p>영업운영 방침</p>
                  <p>공지사항</p>
               </div>
               <Footer />
            </div>
        );
    }
}

export default About;